.header_wraper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 54px;
}
.logo-film > svg,
.logo-film > svg,
.logo-film > span,
.logo-film > span {
  transition: all 0.3s ease-in-out;
}
.logo-film:hover > svg,
.logo-film:focus > svg,
.logo-film:hover > span,
.logo-film:focus > span {
  color: #ff001b;
  stroke: #ff001b;
  text-shadow: 0 0 15px #ff001b;
}
.logo_header {
  stroke: #ffffff;
  margin-right: 10px;
}
.logo_name {
  display: none;
}
.logo_container > a {
  display: flex;
  align-items: center;
}
.navigation_list {
  display: flex;
}
.navigation_list_item {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  line-height: 1.17px;
  font-size: 12px;
  text-transform: uppercase;
  &:not(:last-child) {
    margin-right: 32px;
  }
}
.header_search_form {
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
}
.search_form {
  position: relative;
}
.form-input {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: #ffffff;
  width: 280px;
  position: relative;
  border-bottom: 0.5px solid #ffffff;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: 0;
  background: transparent;
  padding: 10px 10px;
  &:focus-within {
    border-bottom: 1px solid #ff001b;
    box-shadow: 0 0 15px #ff001b;
    border-radius: 5px;
  }
  // @include tablet {
  //   width: 336px;
  // }
  // @include desktop {
  //   width: 394px;
  // }
}
.form-input::placeholder {
  color: #ffffff;
}
.search-btn {
  stroke: #ffffff;
  fill: #ffffff;
  position: absolute;
  width: 22px;
  height: 22px;
  bottom: 10px;
  right: 10px;
  border: none;
  border-radius: 50%;
  background: url(../images/header-main/search.svg);
  background-repeat: no-repeat;
  background-position: center;
  transform: scale(1);
  // @include transition(transform);
  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}
.site_nav_link_current {
  position: relative;
  display: block;
  &::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    transform: translateY(1px);
    display: block;
    width: 100%;
    height: 3px;
    border: none;
    background-color: #ff001b;
    transition: width 0.2s ease-in-out;
  }
  &:hover::after,
  &:focus::after {
    width: 0;
  }
}
